行くことができない、 プロスポーツ選手でさえ練習することができません。コロナ太りと検索すると記事がたくさん出てくるように多くの人が運動不足になっています。 肥満体になると、健康でなくなる、服のサイズが合わなくなるなど日常生活で様々な障害が発生します。 多くの人が運動不足を解消したいと思っているはずです。 また、これから夏に入ると暑くなり、肌の露出が多い服を着る機会が増えると思いますが、体がたるんでいては格好悪いでしょう。 しかし、新型コロナウイルスの感染が怖く、外に出て運動することは中々できないと思います。そこで、私たちは新型コロナウイルスの感染防止を考慮して在宅で運動不足を解消するために、自宅で誰でも気軽に運動できるサービスを開発しようと考えました。")]),_c('p',[_vm._v("自宅でできる運動は筋トレなどさまざまあると思いますが、 私たちはラジオ体操に焦点を当てました。 多くの人は、学校の体育の授業で友達とラジオ体操を一緒にやった、または町内会で行われている朝の体操で町の人とラジオ体操を一緒にやった経験があると思います。 老若男女の誰もが知っているかつ気軽にできるラジオ体操だからこそ、在宅で行うハードルは低いと思います。 ")]),_vm._v(" 家の中でラジオ体操や筋トレをやろうと意気込んでも、三日坊主になり長続きするのは難しいです。これは1人でやっているため、他人の目や強制力がないことが原因だと言えます。 他人と一緒にやれば、あの子が続けているから私も頑張って続けようと思えるはずです。 誰かと一緒に時間を共有することが長続きの秘訣だと言えます。 私たちは、同じ動画を共有することで擬似的に他人と一緒に何かをしている感覚が生み出せるのではないかと考えました。"),_c('br'),_c('p',[_vm._v("新型コロナウイルスの感染防止のために在宅、肥満解消のために運動をしたい、持続するために他人と時間を共有したいという課題を解決するために、 私たちはweb上で他人とラジオ体操動画を共有して一緒に在宅でラジオ体操をするサービスを開発しました。 ")]),_c('p',[_vm._v(" サービスをお使い頂いたら、サービスの品質向上のためにアンケートにご協力ください。 ")]),_c('p',[_c('input',{attrs:{"type":"button","name":"buttonName","id":"question-button","value":"アンケート","onClick":"window.open('https://docs.google.com/forms/d/e/1FAIpQLSeeWIQ5NyHNrhWfj9K_ZJS8sZ_wD0ZJTYCOpQr1qm9KkUDLBQ/viewform?usp=sf_link','_blank')"}})])])])])}]

